import React, { useCallback, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import styles from './Login.module.css';
import { fetchData } from './DataManager';
import { __DEV__, User } from './Constants';

interface Props {
    onLogin: (user: User) => void;
}
const Login: React.FunctionComponent<Props> = (p: Props) =>
{
    let onLogin = p.onLogin,
        [username, setUsername] = useState(''),
        [password, setPassword] = useState('');

    const login = useCallback(async () => {
        let value = await fetchData('authenticate', undefined, {
                            method: 'POST',
                            body: JSON.stringify({
                                Username: username,
                                Password: password
                            })
                        });

        if (__DEV__)
        {
            localStorage.setItem('user', JSON.stringify(value));
        }
        onLogin(value);
    }, [ onLogin, username, password ]);

    const onKeyPress = useCallback((e: React.KeyboardEvent) => {
        if (e.key === 'Enter')
        {
            login();
        }
    }, [ login ]);

    return (
        <Grid
            className={styles.container}
            container
            spacing={16}
            justify="center"
            alignItems="center"
            direction="row"
        >
            <Grid item>
                <h1 className={styles.logo}>
                    EyeTracking
                </h1>
                <Paper className={styles.loginBox}>
                    <div className={styles.textWrap}>
                        <TextField className={styles.textField} label="Username" onChange={e => setUsername(e.target.value)} onKeyPress={onKeyPress} />
                    </div>
                    <div className={styles.textWrap}>
                        <TextField className={styles.textField} label="Password" onChange={e => setPassword(e.target.value)} onKeyPress={onKeyPress} />
                    </div>
                    <div className={styles.buttonWrap}>
                        <Button variant="contained" color="primary" onClick={login}>
                            Login
                        </Button>
                    </div>
                </Paper>
            </Grid>
        </Grid>
    );
}

export default Login;