import { __DEV__ } from './Constants';

const EnableCaching = __DEV__ && false;

const cache: { [key: string]: any } = {};

export const fetchData = async (url: string, token: string | undefined, options?: { [ key: string ]: any }) =>
{
    if (cache[url])
    {
        return cache[url];
    }

    let resultData = EnableCaching && localStorage.getItem(`_cache_${url}`);
    if (resultData)
    {
        return JSON.parse(resultData);
    }
    else
    {
        options = options || {};
        options.headers = options.headers || {};
        options.headers['Content-Type'] = 'application/json';
        if (token)
        {
            options.headers['Authorization'] = 'Bearer ' + token;
        }

        let result = await fetch(`https://eyenetservice20190228013441.azurewebsites.net/v1/${url}`, options);

        if (result.ok)
        {
            let data = await result.json();

            cache[url] = data;
            if (EnableCaching)
            {
                localStorage.setItem(`_cache_${url}`, JSON.stringify(data));
            }
            return data;
        }

        // TODO: Error handling
    }
};