import React, { useState, useEffect } from 'react';
import styles from'./SessionDetails.module.css';
import { SessionResults, SessionData } from './Constants';
import { Typography, Card, CardContent, Select, MenuItem } from '@material-ui/core';
import IconArrowUp from '@material-ui/icons/ArrowUpward';
import IconArrowDown from '@material-ui/icons/ArrowDownward';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import { fetchData } from './DataManager';

interface Props
{
    user: any;
    sessions: SessionData[];
}

interface HighchartLine {
    name: string,
    data: number[];
}

type Algorithm = 'Raw' | 'Sum';
const Algorithms = [ 'Raw', 'Sum' ];

const getChartOptions = (lines: HighchartLine[], sessions: SessionResults[]): Highcharts.Options =>
{
    // let titles = sessions.map(res => `<b>${res.session.name}</b>: ${res.session.testName}`),
    //      title = titles.length > 1 ? ('Comparing: ' + titles.join(', ')) : titles[0];

    return {
        chart: {
            zoomType: 'x'
        },
        title: {
            text: ''
        },
        xAxis: {
            title: {
                text: 'Time'
            }
        },
        yAxis: {
            title: {
                text: 'Workload'
            }
        },
        series: lines.map(line => ({
            type: 'line',
            name: line.name,
            data: line.data
        }))
    };
}

const Subjects: React.FunctionComponent<Props> = (p: Props) =>
{
    const { sessions } = p,
          token = p.user.token;

    const [ results, setResults ] = useState<SessionResults[] | undefined>(undefined);
    const [ algorithm, setAlgorithm ] = useState<Algorithm>('Sum');
    const [ chartOptions, setChartOptions ] = useState<Highcharts.Options>(getChartOptions([], []));

    useEffect(() =>
    {
        if (results)
        {
            let lines: HighchartLine[] = [];

            let processResults: (resultData: number[]) => number[];

            if (algorithm === 'Sum')
            {
                processResults = (resultData: number[]) => {
                    var data: number[] = [];
                    resultData.reduce((a, b, i) => { return data[i] = +(a + b).toFixed(2); }, 0);
                    return data;
                };
            }
            else
            {
                processResults = (resultData: number[]) => resultData;
            }

            results.forEach((result, i) =>
            {
                let session = result.session,
                    resultData = result.resultData.map(d => (Math.max(0, d.leftValue) + Math.max(d.rightValue, 0)) / 2),
                    data = processResults(resultData);

                lines.push({
                    name: session.testName,
                    data
                });
            });

            setChartOptions(getChartOptions(lines, results));
        }
    }, [ results, algorithm ]);

    useEffect(() =>
    {
        Promise.all(sessions.map(async (session) =>
        {
            const processData = (resultData: any) =>
            {
                return {
                    session,
                    resultData
                };
            };

            let data = await fetchData(`result/workloaddata/${session.resultId}`, token);

            return processData(data);
        })).then((res: SessionResults[]) =>
        {
            setResults(res);
        });
    }, [token, sessions]);

    return results ? (
        <div className={styles.sessionDetails}>
            <div className={`${styles.card} ${styles.personDetails}`}>
                {sessions.map(session => (
                    <span key={session.resultId}>
                        <div className={styles.name}>{session.name}</div>
                        <div className={styles.light}>{session.rankName}</div>
                        <div className={styles.light}>{session.platformName}</div>
                        <div className={styles.light}>{session.dateString}</div>
                    </span>
                ))}
            </div>
            <Card className={`${styles.card} ${styles.chartCard}`}>
                <CardContent className={styles.cardContent}>
                    <Typography className={styles.title} color="textSecondary">
                        Results
                    </Typography>
                    <Select
                        className={styles.selectAlgorithm}
                        value={algorithm}
                        onChange={(e) => setAlgorithm(e.target.value as Algorithm)}
                    >
                        {Algorithms.map(alg => <MenuItem key={alg} value={alg} className={styles.menuItem}>{alg}</MenuItem>)}
                    </Select>
                    <HighchartsReact
                        className={styles.chart}
                        highcharts={Highcharts}
                        options={chartOptions}
                    />
                </CardContent>
            </Card>
            <Card className={`${styles.card} ${styles.highlights}`}>
                <CardContent className={styles.cardContent}>
                    <Typography className={styles.title} color="textSecondary">
                        Highlights
                    </Typography>
                    <Typography className={styles.highlightsContent}>
                        <span className={styles.highlightsText}>Workload</span><IconArrowUp fontSize="small" className={`${styles.icon} ${styles.arrowUp}`} /> 3%
                        </Typography>
                    <Typography className={styles.highlightsContent}>
                        <span className={styles.highlightsText}>Time</span><IconArrowDown fontSize="small" className={`${styles.icon} ${styles.arrowDown}`} /> 2%
                        </Typography>
                    <Typography className={styles.highlightsContent}>
                        <span className={styles.highlightsText}>Scan Efficiency</span><IconArrowUp fontSize="small" className={`${styles.icon} ${styles.arrowUp}`} /> 5%
                        </Typography>
                </CardContent>
            </Card>
        </div>
    ) : (
        <div className={styles.loading}>
            <div>Loading...</div>
        </div>
    );
}

export default Subjects;